.swiper {
  &__more {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: all .5s;
  }

  &__info {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    padding: 24px 0;
    background: rgba($color: #000000, $alpha: .3);
    font-size:18px;
    color:#FFFFFF;
    line-height:1;
  }

  &__button {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 25px;
    padding-bottom: 25px;
    background: rgba($color: #000000, $alpha: .5);
    color: #FFFFFF;
    z-index: 1;

    &:hover {
      color: #FFFFFF;
    }

    .iconfont {
      font-size: 30px;
    }

    &-720 {
      margin: 0 8px 0 20px;
    }

    &-text {
      display: flex;
      flex-direction: column;
      font-size:12px;
      font-weight:400;
      line-height: 1.2;
    }
  }

  &:hover {
    .swiper {
      &__more {
        opacity: 1;
      }
    }
  }

  &__icon {
    font-size: 40px;
  }
}

@media (max-width: 767px) {
  .swiper {
    &__info {
      padding: 12px 0;
    }

    &__button {
      padding-top: 10px;
      padding-bottom: 10px;
    }
  }
}

@media (max-width: 575px) {
  .swiper {
    &__info {
      display: none;
    }
  }
}
